// 密码正则  字母数字特殊字符  8-16
const regPassword = /^[0-9a-zA-Z]{8,16}$/;
const regPhone = /^1[3|4|5|7|8][0-9]{9}$/;

export const checkMobile = function (phone) {
  if(!(regPhone.test(phone))){
    return false;
  } else return true
}

export const checkPassword = function (pass) {
  if(!(regPassword.test(pass))){
    return false;
  } else return true
}

export function is_weixin() {
  var ua = window.navigator.userAgent.toLowerCase();
  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
    return true
  } else {
    return false
  }
}



