export default {
	name: 'BottomBtn',
	data () {
		return {
			btnStyle: {
				'bottom-btn': true,
				'disable': this.disable
			}
		}
	},
	props: {
		value: {
			type: String,
			required: true
		},
		disable: {
			default: false,
			type: Boolean
		}
	}
}



