
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {checkMobile} from "../../utils/assist/regExp";

export default {
name: "verification-code",
props: {
placeholder: {
default: '',
type: String
},
codeplaceholder: {
default: '发送验证码',
type: String
},
label: {
type: String
}
},
components: {
XButton,
XInput
},
data() {
return {
phonenum: null,
codenum: '',
show: true,
count: '',
timer: null,
}
},
watch: {
phonenum(val, oldVal) {
this.$emit('getphone', val)
},
codenum(val, oldVal) {
this.$emit('update:code', val)
},
},
methods: {
getCode() {
if (!this.$refs.input.valid) {
return
}
if (!checkMobile(this.phonenum)) {
return
}

if (!this.timer) {
this.count = 60;
this.show = false;
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= 60) {
this.count--;
} else {
this.show = true;
clearInterval(this.timer);
this.timer = null;
}
}, 1000)
this.$emit('getcode', this.phonenum)
}
}
}
}



